.user__data {
  padding-top: 0;
}

.user__btns {
  margin-left: 40px;
  margin-top: 40px;
  padding-bottom: 85px;
  display: flex;
  gap: 32px;
}

@media screen and (max-width: 768px) {
  .user__btns {
    margin-top: 30px;
    margin-left: 33px;
  }
}

@media screen and (max-width: 600px) {
  .user__btns {
    margin-left: 0;
    padding: 0;
    margin-top: 38px;
    padding-bottom: 13px;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
}

.user__btns {
  display: none;
  &_desktop {
    @media screen and (min-width: 769px) {
      display: flex;
    }
  }
  &_tablet {
    @media screen and (max-width: 768px) and (min-width: 601px) {
      display: flex;
    }
  }
  &_mobile {
    @media screen and (max-width: 600px) {
      display: flex;
    }
  }
}
